"use client";

import { useContext, useEffect, useState } from "react";
import { AuthContext } from "../../context/AuthContext";
import { ThemeContext } from "../../context/ThemeContext";
import { useRouter } from "next/navigation";
import dynamic from "next/dynamic";
import { FaPlus, FaUserFriends, FaChevronLeft, FaChevronRight } from "react-icons/fa";

// Load emoji picker dynamically (client-side only)
const Picker = dynamic(() => import("emoji-picker-react"), { ssr: false });

export default function ProfilePage() {
  const { user } = useContext(AuthContext);
  const { theme } = useContext(ThemeContext);
  const router = useRouter();

  const [showForm, setShowForm] = useState(false);
  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");
  const [imageFile, setImageFile] = useState(null);
  const [videoFile, setVideoFile] = useState(null);
  const [showEmojiPicker, setShowEmojiPicker] = useState(false);
  const [message, setMessage] = useState("");
  const [collapsed, setCollapsed] = useState(false);

  useEffect(() => {
    if (!user) router.push("/login");
  }, [user]);

  if (!user) return null;

  const handleCreatePost = async (e) => {
    e.preventDefault();
    if (!title && !content && !imageFile && !videoFile)
      return alert("Add some content!");

    const formData = new FormData();
    formData.append("title", title);
    formData.append("content", content);
    if (imageFile) formData.append("image", imageFile);
    if (videoFile) formData.append("video", videoFile);

    const res = await fetch("/api/posts/create", {
      method: "POST",
      body: formData,
    });

    const data = await res.json();
    if (res.ok) {
      setMessage("Post created successfully!");
      setShowForm(false);
      setTitle("");
      setContent("");
      setImageFile(null);
      setVideoFile(null);
    } else {
      setMessage(data.error || "Something went wrong");
    }
  };

  const onEmojiClick = (emojiData) => {
    setContent((prev) => prev + emojiData.emoji);
  };

  // Dynamic styles based on theme
  const styles = {
    page: {
      display: "flex",
      minHeight: "100vh",
      background: theme === "dark" ? "#121212" : "#fff",
      color: theme === "dark" ? "#f1f1f1" : "#000",
    },
    sidebar: {
      width: collapsed ? "70px" : "220px",
      transition: "width 0.3s",
      padding: "20px 10px",
      background: theme === "dark" ? "#1e1e1e" : "#f0f0f0",
      display: "flex",
      flexDirection: "column",
      gap: "15px",
      borderRight: theme === "dark" ? "1px solid #333" : "1px solid #ccc",
      position: "sticky",
      top: 0,
      height: "100vh",
    },
    sidebarBtn: {
      display: "flex",
      alignItems: "center",
      gap: collapsed ? "0" : "10px",
      padding: "10px",
      background: theme === "dark" ? "#333" : "#ddd",
      color: theme === "dark" ? "#fff" : "#000",
      border: "none",
      borderRadius: "6px",
      cursor: "pointer",
      width: "100%",
      justifyContent: collapsed ? "center" : "flex-start",
    },
    collapseBtn: {
      marginTop: "auto",
      padding: "6px",
      background: "transparent",
      border: "none",
      cursor: "pointer",
      color: theme === "dark" ? "#fff" : "#000",
      display: "flex",
      justifyContent: collapsed ? "center" : "flex-end",
    },
    main: {
      flex: 1,
      padding: "30px",
    },
    card: {
      marginBottom: "20px",
      padding: "20px",
      background: theme === "dark" ? "#1e1e1e" : "#eee",
      borderRadius: "8px",
    },
    form: {
      marginTop: "20px",
      padding: "20px",
      background: theme === "dark" ? "#1e1e1e" : "#f7f7f7",
      borderRadius: "8px",
      display: "flex",
      flexDirection: "column",
      gap: "10px",
    },
    input: {
      padding: "10px",
      border: "1px solid #ccc",
      borderRadius: "5px",
      background: theme === "dark" ? "#2b2b2b" : "#fff",
      color: theme === "dark" ? "#f1f1f1" : "#000",
    },
    textarea: {
      padding: "10px",
      border: "1px solid #ccc",
      borderRadius: "5px",
      background: theme === "dark" ? "#2b2b2b" : "#fff",
      color: theme === "dark" ? "#f1f1f1" : "#000",
    },
    emojiBtn: {
      padding: "6px 12px",
      background: "#ffecb3",
      borderRadius: "5px",
      border: "none",
      cursor: "pointer",
      width: "fit-content",
    },
    submitBtn: {
      padding: "10px 18px",
      background: "green",
      color: "white",
      borderRadius: "6px",
      border: "none",
      cursor: "pointer",
    },
    message: {
      marginTop: "15px",
      color: "green",
    },
  };

  return (
    <div style={styles.page}>
      {/* Sidebar */}
      <div style={styles.sidebar}>
        <button onClick={() => setShowForm(!showForm)} style={styles.sidebarBtn}>
          <FaPlus />
          {!collapsed && <span>Create Post</span>}
        </button>
        <button onClick={() => alert("Add Friends clicked!")} style={styles.sidebarBtn}>
          <FaUserFriends />
          {!collapsed && <span>Add Friends</span>}
        </button>

        <button
          onClick={() => setCollapsed(!collapsed)}
          style={styles.collapseBtn}
        >
          {collapsed ? <FaChevronRight /> : <FaChevronLeft />}
        </button>
      </div>

      {/* Main Content */}
      <div style={styles.main}>
        <h1>Your Profile</h1>

        {/* User Info */}
        <div style={styles.card}>
          <p><strong>Name:</strong> {user.name || "No Name"}</p>
          <p><strong>Email:</strong> {user.email}</p>
          <p><strong>Role:</strong> {user.role}</p>
          <p><strong>Joined:</strong> {new Date(user.createdAt).toLocaleDateString()}</p>
        </div>

        {/* Create Post Form */}
        {showForm && (
          <form onSubmit={handleCreatePost} style={styles.form} encType="multipart/form-data">
            <input type="text" placeholder="Post title" value={title} onChange={(e) => setTitle(e.target.value)} style={styles.input} />
            <textarea placeholder="Write your post..." value={content} onChange={(e) => setContent(e.target.value)} rows={4} style={styles.textarea} />

            {/* Emoji Picker */}
            <button type="button" onClick={() => setShowEmojiPicker(!showEmojiPicker)} style={styles.emojiBtn}>
              {showEmojiPicker ? "Close Emojis" : "Add Emoji"}
            </button>
            {showEmojiPicker && <Picker onEmojiClick={onEmojiClick} theme={theme === "dark" ? "dark" : "light"} />}

            {/* File Uploads */}
            <div style={{ display: "flex", gap: "10px", flexDirection: "column" }}>
              <input type="file" accept="image/*" onChange={(e) => setImageFile(e.target.files[0])} />
              <input type="file" accept="video/*" onChange={(e) => setVideoFile(e.target.files[0])} />
            </div>

            <button type="submit" style={styles.submitBtn}>Publish</button>
          </form>
        )}

        {/* Message */}
        {message && <p style={styles.message}>{message}</p>}
      </div>
    </div>
  );
}
